import { _decorator, Component, Node, SpriteFrame, Sprite, resources, Prefab, instantiate } from 'cc';
import HTTP from '../utils/HTTP';
const { ccclass, property } = _decorator;

@ccclass('game_sgj')
export class game_sgj extends Component {

    //     //用户下注
    // typedef struct 
    // {
    //     LONGLONG		lJettonScore[8];						//加注数目
    // }CMD_C_SGJ_PlaceJetton;
    betScore: number[] = [0, 0, 0, 0, 0, 0, 0, 0]

    //客户端消息----下注
    betdata =
        {
            type: 'CMD_C_SGJ_PlaceJetton',
            data: this.betScore,
        }

    //客户端消息----猜大小,//猜大小，0为小（0-7），1为大（8-15）
    bigsmall =
        {
            type: 'CMD_C_SGJ_BigSmall',
            data: 0, //赌注
        }


    start() {
        console.log('betScore', this.betdata);
        globalThis.curgame = this;
    }

    update(deltaTime: number) {

    }

    //接受消息
    onMessage(type, data) {
        console.log('收到服务器消息：', data);
    }

    //发送消息
    sendMssage(type: any, data: any) {
        let msg = {
            type: type,
            data: data,
        }
        HTTP.getInstance().send(JSON.stringify(msg));
    }
}